import {
	getForBalance,
	getForBill,
	checkForTaskList,
	signinMsg,
	signin
} from '../../api/api.js';
export default {
	data() {
		return {
			assetInfo: '',
			listInfo: '',
			labelList: 'budget',
			budgetText: 'black',
			taskText: 'gray',
			pageSize: 10,
			isShow: false,
			sing: [{
					value: "+1",
					title: "第一天",
					isShow: false
				},
				{
					value: "+2",
					title: "第二天",
					isShow: false
				},
				{
					value: "+3",
					title: "第三天",
					isShow: false
				},
				{
					value: "+5",
					title: "第四天",
					isShow: false
				},
				{
					value: "+5",
					title: "第五天",
					isShow: false
				},
				{
					value: "+10",
					title: "第六天",
					isShow: false
				},
				{
					value: "+10",
					title: "第七天",
					isShow: false
				},
			],
			singDay: "",
			force: ''
		}
	},
	methods: {
		getBalanceData() {
			getForBalance().then(data => {
				this.assetInfo = data;
			});
		},
		getBillData() {
			getForBill(0).then(data => {
				this.listInfo = '';
				this.listInfo = data;
				// console.log(this.listInfo);
			});
		},
		getTaskData() {
			checkForTaskList().then(data => {
				this.listInfo = '';
				this.listInfo = data;
				// console.log(this.listInfo);
			});
		},
		changeList(chooseItem) {
			if (chooseItem == 'budget') {
				this.labelList = 'budget';
				this.budgetText = 'black';
				this.taskText = 'gray';
				this.getBillData();
			} else {
				this.labelList = 'task';
				this.budgetText = 'gray';
				this.taskText = 'black';
				this.getTaskData();
			}
		},
		loadMore() {
			getForBill(this.pageSize).then(data => {
				if (data == null) {
					this.isShow = true;
					return
				}
				this.listInfo = this.listInfo.concat(data);
				this.pageSize += 10
			});
		},
		singIn() {
			if (this.singDay == 7) {
				this.singDay = 0
				signin(this.force, this.singDay + 1).then(data => {
					if (data != null) {
						console.log(data)
						this.show();
						this.getSigninMsg()
						uni.showToast({
							icon: 'none',
							title: '簽到成功'
						})
						this.getBillData();
						this.getBalanceData()
					}
				})
			}
			signin(this.force, this.singDay + 1).then(data => {
				if (data != null) {
					console.log(data)
					this.show();
					this.getSigninMsg()
					uni.showToast({
						icon: 'none',
						title: '簽到成功'
					})
					this.getBillData();
					this.getBalanceData()
				}
			})

		},
		getSigninMsg() {
			signinMsg().then(data => {
				console.log(data)
				this.singDay = data
				switch (data) {
					case 0:
						this.force = 1
						break
					case 1:
						this.force = 2
						break
					case 2:
						this.force = 3
						break
					case 3:
						this.force = 5
						break
					case 4:
						this.force = 5
						break
					case 5:
						this.force = 10
						break
					case 6:
						this.force = 10
						break

				}
				this.show()
			})
		},
		show() {
			switch (this.singDay) {
				case 0:
					this.sing[0].value = "+1"
					this.sing[0].isShow = false
					this.sing[1].value = "+2"
					this.sing[1].isShow = false
					this.sing[2].value = "+3"
					this.sing[2].isShow = false
					this.sing[3].value = "+5"
					this.sing[3].isShow = false
					this.sing[4].value = "+5"
					this.sing[4].isShow = false
					this.sing[5].value = "+10"
					this.sing[5].isShow = false
					this.sing[6].value = "+10"
					this.sing[6].isShow = false
					break
				case 1:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					break
				case 2:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					this.sing[1].value = "√"
					this.sing[1].isShow = true
					break
				case 3:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					this.sing[1].value = "√"
					this.sing[1].isShow = true
					this.sing[2].value = "√"
					this.sing[2].isShow = true
					break
				case 4:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					this.sing[1].value = "√"
					this.sing[1].isShow = true
					this.sing[2].value = "√"
					this.sing[2].isShow = true
					this.sing[3].value = "√"
					this.sing[3].isShow = true
					break
				case 5:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					this.sing[1].value = "√"
					this.sing[1].isShow = true
					this.sing[2].value = "√"
					this.sing[2].isShow = true
					this.sing[3].value = "√"
					this.sing[3].isShow = true
					this.sing[4].value = "√"
					this.sing[4].isShow = true
					break
				case 6:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					this.sing[1].value = "√"
					this.sing[1].isShow = true
					this.sing[2].value = "√"
					this.sing[2].isShow = true
					this.sing[3].value = "√"
					this.sing[3].isShow = true
					this.sing[4].value = "√"
					this.sing[4].isShow = true
					this.sing[5].value = "√"
					this.sing[5].isShow = true
					break
				case 7:
					this.sing[0].value = "√"
					this.sing[0].isShow = true
					this.sing[1].value = "√"
					this.sing[1].isShow = true
					this.sing[2].value = "√"
					this.sing[2].isShow = true
					this.sing[3].value = "√"
					this.sing[3].isShow = true
					this.sing[4].value = "√"
					this.sing[4].isShow = true
					this.sing[5].value = "√"
					this.sing[5].isShow = true
					this.sing[6].value = "√"
					this.sing[6].isShow = true
					break
			}
		}
	},
	onLoad() {
		this.getBalanceData();
		this.getBillData()
		this.getSigninMsg()
	}
}
